import './index.scss';
import React from 'react';
import { Call, CasesSharp, CellWifiSharp, Home, Person } from '@mui/icons-material';
import { useLocation } from 'react-router-dom';

interface SideBarProps {
    children?: React.ReactNode;
};

export const SideBar = (props: SideBarProps) => {

    const history = useLocation();

    const [activeClass, setActiveClass] = React.useState<"home" | "about" | "portfolio" | "blog" | "contact" | string>(history.pathname.replace(/\//g, '') ?? "home");

    return (
        <>
            <div className="header-holder services-section text-center animate__animated animate__zoomIn">
                <div className="navigation">
                    <ul className="nav">
                        <li className={`list ${activeClass === "home" ? "active" : ""}`}>
                            <a href="/#/#/home" className={`${activeClass === "home" ? "active" : ""}`} onClick={() => setActiveClass("home")}>
                                <span className="icon">
                                    <Home />
                                </span>
                                <div className="social__tooltip social__tooltip-bottom">Home</div>
                            </a>
                        </li>
                        <li className={`list ${activeClass === "about" ? "active" : ""}`}>
                            <a href="/#/about" className={`${activeClass === "about" ? "active" : ""}`} onClick={() => setActiveClass("about")}>
                                <span className="icon">
                                    <Person />
                                </span>
                                <div className="social__tooltip social__tooltip-bottom">About</div>
                            </a>
                        </li>
                        <li className={`list ${activeClass === "portfolio" ? "active" : ""}`}>
                            <a href="/#/portfolio" className={`${activeClass === "portfolio" ? "active" : ""}`} onClick={() => setActiveClass("portfolio")}>
                                <span className="icon">
                                    <CasesSharp />
                                </span>
                                <div className="social__tooltip social__tooltip-bottom">Portfolio</div>
                            </a>
                        </li>
                        <li className={`list ${activeClass === "blog" ? "active" : ""}`}>
                            <a href="/#/blog" className={`${activeClass === "blog" ? "active" : ""}`} onClick={() => setActiveClass("blog")}>
                                <span className="icon">
                                    <CellWifiSharp />
                                </span>
                                <div className="social__tooltip social__tooltip-bottom">Blog</div>
                            </a>
                        </li>
                        <li className={`list ${activeClass === "contact" ? "active" : ""}`}>
                            <a href="/#/contact" className={`${activeClass === "contact" ? "active" : ""}`} onClick={() => setActiveClass("contact")}>
                                <span className="icon">
                                    <Call />
                                </span>
                                <div className="social__tooltip social__tooltip-bottom">Contact</div>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </>
    )
};